import React from 'react';
import {Button,Icon} from 'antd';
import ReactDOM from 'react-dom';


class Modal extends React.Component{
    constructor(props){
       super(props)
       this.state={
           open:false
       }
    }
    /**
     * @method closeModal mask
     */
    closeModal(){
        this.props.onCancle && this.props.onCancle()
    }
    componentWillReceiveProps(nextProps){
      if(nextProps.open&&!this.props.open){
          this.node = document.createElement('div'); //  DOM
          this.node.className = 'ReactModal'; //  ClassName
          document.getElementsByTagName('body')[0].appendChild(this.node) //  body   className  div
          //  render
          const style = require('./modal.less'); // css 
          const { children, ...rest} = nextProps;
          let leMmodalStyle = {
              width: nextProps.width ? nextProps.width:'520px'
          }
          let modal = (
              <div className={"len-modal-container"}>
                  <div className={"mask"} 
                    //    {...rest}
                       onClick={()=>{this.closeModal()}}                     
                 >
                  </div>
                  <div className="modal-wrap">
                    <div className="len-modal animated slideInDown" style={leMmodalStyle}>
                          <div className="modal-title">
                            {nextProps.title ? nextProps.title:''}
                            <Icon type='close' onClick={() => { this.closeModal() }}/>
                          </div>
                          <div className="modal-body">
                              {nextProps.children}
                          </div>
                          <div className="modal-footer">
                              <Button onClick={() => { this.closeModal() }}></Button>
                              <Button type="primary"></Button>
                          </div>
                    </div>
                      
                  </div>
                  
              </div>
          );
          //  Modal  Dom
          let allClass = document.getElementsByClassName('ReactModal');
          ReactDOM.render(modal, allClass[allClass.length - 1]) // ModalModaldiv
          //  Modal  render 
        } 
        if (this.props.open && !nextProps.open) { // 
            ReactDOM.unmountComponentAtNode(this.node) //  react-dom unmountComponentAtNodeModal
            // divdivbody
            // document.getElementsByTagName('body')[0].removeChild(document.getElementsByClassName('ReactModal')[0])
        }
    }
    render(){
        return null
    }
}
export default Modal;