import React from 'react';
import {Button,Form,Input,Row,Col} from 'antd';
const FormItem = Form.Item;
require("./login.less");

@Form.create()
class LoginView extends React.Component{
    constructor(props){
        super(props)
        this.state={
            userinfo:{
                name:"username",
                password:"123456"
            }
        }
    }
    componentDidMount(){
      this.initMap()
    }
    initMap(){
      var map = new BMap.Map("container"); 
      var point = new BMap.Point(116.404, 39.915); 
      map.centerAndZoom(point, 15); //
      map.enableScrollWheelZoom(true); //
      map.addControl(new BMap.NavigationControl()); //
      var geolocation = new BMap.Geolocation();
      geolocation.getCurrentPosition(function(r){ //
        if(this.getStatus() == BMAP_STATUS_SUCCESS){
          var mk = new BMap.Marker(r.point);
          map.addOverlay(mk);
          map.panTo(r.point);
          console.log(''+r.point.lng+','+r.point.lat);
        }
        else {
          console.log('failed'+this.getStatus());
          geolocation.enableSDKLocation();  //SDK
          geolocation.getCurrentPosition(function(r){
            if(this.getStatus() == BMAP_STATUS_SUCCESS){
              var mk = new BMap.Marker(r.point);
              map.addOverlay(mk);
              map.panTo(r.point);
              console.log(''+r.point.lng+','+r.point.lat);
            }
            else {
              console.log('failed'+this.getStatus());
              var myCity = new BMap.LocalCity();
              myCity.get((result)=>{
                var cityName = result.name;
                map.setCenter(cityName);
                console.log(":"+cityName);
              }); 
            }        
          });
        }        
      });
    }
    loginIn(){
        this.props.history.push('/main')
    }
    render(){
        const {userinfo} = this.state;
        const { getFieldDecorator } = this.props.form;
        const formItemLayout = {
        labelCol: {
            xs: { span: 24 },
            sm: { span: 7 },
            md: { span: 6 },
        },
        wrapperCol: {
            xs: { span: 24 },
            sm: { span: 12 },
            md: { span: 18 },
        },
        };
        return(
        <div className="loginWrap">
          <div className="formWrap">
            <Form>
              <h3></h3>  
              <FormItem {...formItemLayout} label="">
              {getFieldDecorator('name', { rules: [{ required: true, message: '' }],initialValue:userinfo.name})(
                  <Input />
              )}
              </FormItem>
              <FormItem {...formItemLayout} label="">
                {getFieldDecorator('password', { rules: [{ required: true, message: '' }],initialValue:userinfo.password})(
                  <Input type="password"/>
                )}
              </FormItem>
              <Row>
                  <Col {...formItemLayout.labelCol}></Col>
                  <Col {...formItemLayout.wrapperCol}>
                      <Button
                        onClick={()=>{this.loginIn()}} 
                        type="primary"
                      >
                        
                      </Button>
                  </Col>
              </Row>
            </Form>
          </div>
          <div className="container">
            <span></span>
            <div id="container"></div> 
          </div>
        </div>)
    }
}
export default LoginView